html, body {
    margin: 0;
    padding: 0;
  }

.main-container {
    width: 100%;
    height: 100%;
    max-width: 100%;
    overflow: hidden;
    position: relative;
    background-size: cover;
    background-attachment: fixed;
    
  }

  /* ------------ HEADER -------------- */

header img, header nav, header , .nav-container {
    display: inline-block;
    vertical-align: middle;
  }

header {
    background-color: #fff;
    width: 100%;
    /* position: fixed; */
    display: block;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  }

  #nav {
    width: 60%;
    height: auto;
    max-width: 100%;
    line-height: 200%;
  }

  header .header-logo {
    width: 150px;
    height: 120px;
    margin: 0 10px;
    padding: 8px;
  }

  ul {
    list-style-type: none;
    margin: 0 auto;
    width: 100%;
    text-align: center;
  }
  
  ul li {
    display: inline;
    /* float: left; */
    text-decoration: none;
    padding-right: 20px;
  }

  #nav a {

    padding: 10px 10px;
    text-decoration: none;
    font-size: 14px;
    color: rgb(0, 0, 0); 
  } 

  .main-container .content {
    display: flex;
    flex-wrap: wrap;
  }

  .mySlides img {
    width:100%;
    height:100vh;
    transition: opacity 300ms ease-in-out;
  }
  
  
  .banner-container {
    width: 100%;
    height: 100%;
    max-width: 100%;
    position: relative;
  }

  .banner-container .after {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    max-width: 100%;
    background: rgba(0, 0, 0, .6);
}

.heading-container {
    position: absolute;
    top: 30%;
    left: 10%;
    width: 75%;
    padding: 20px;
    margin: 0 auto;
    /* background-color: aqua; */
}

.heading-container p:nth-child(1) {
    font-size: 50px;
    color: #fff;
    text-align: center;
}

.heading-container p:nth-child(2) {
    display: block;
    width: 50%;
    font-size: 25px;
    color: #fff;
    margin: 20px auto;
    text-align: center;
}


.heading-container div {
    position: relative;
    display: block;
    margin: 0px auto;
    padding: 15px;
    font-weight: 300;
    font-size: 17px;
    font-family: Verdana;
    color: #fff;
    width: 160px;
    height: auto;
    text-align: center;
    background-color: rgb(160, 13, 13);
  }

  .home-main-content {
      width: 100%;
      height:auto;
      margin: 0;
      
  }

  .home-main-content .content-row-1{
    color: rgba(0, 0, 0, 0.7);
  }

  .content-row-1 .content{
    background-color: #fff;
    width: 100%;
    height: auto;
  }

  .home-main-content .content-row-1 .content-row-1-underlay {
    background-color: rgba(254, 245, 231, 0.4);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    margin: 0 auto;
  }

  .home-main-content .content-row-1 .top-heading{
      display: block;
      padding: 33px 0 0 0;
      font-size: 25px;
      letter-spacing: 5px;
      text-align: center;
  }
  .home-main-content .content-row-1 .row-1{
    width: 75%;
    height: auto;
    display: flex;
    margin: 0 auto;
    padding-bottom: 40px;  
    flex-direction: row;
  }

  .home-main-content .content-row-1 .row-1 .col-1{
    width: 40%;
    height: auto;
    margin: 0 30px;
  }

  .content-rule { 
    width:2px;
    height:170px;
    background-color:rgb(223, 204, 177);
  }

  .home-main-content .content-row-1 .row-1 .col-1 p {
    display: block;
    font-size: 14px;
    text-align: center;

  }

  .home-main-content .content-row-1 .row-1 .col-1 .scope {
    font-weight: bold;
    line-height: 0ch;
  }

  .home-main-content .content-row-1 .row-1 .col-1 .heading {
    font-weight: bold; 
    font-size: 23px;
  }

  .home-main-content .content-row-2 {
    width: 100%;
    height: auto;
  }

  .home-main-content .content-row-2 .row-1 {

    width: 70%;
    height: auto;
    margin: 0 auto;
    padding:50px;
    flex-direction: column;
  }

  .home-main-content .content-row-2 .row-1 p:nth-child(1){
      display: block;
      text-align: center;
      width: 50%;
      padding: 20px 0px 0px 20px;
      margin: 0 auto;
      letter-spacing: 5px;
      font-size: 25px;
  }

  .home-main-content .content-row-2 .row-1 p:nth-child(2){
    display: block;
    text-align: center;
    width: 70%;
    font-size: 15px;
    padding: 30px 0px 0px 20px;
    color: rgba(0, 0, 0, 0.7);
    margin: 0 auto;
  }
  .home-main-content .content-row-2 .row-2  {
      width: 70%;
      height: auto;
      display: flex;
      margin: 50px auto;
      flex-direction: row;
      flex-wrap: nowrap;
      /* background-color: aqua; */
  }

  .home-main-content .content-row-2 .row-2 .card  {
    width: 30%;
    height: auto;
    margin: 20px auto;
    color: rgba(0, 0, 0, 0.7);
    background-color: rgba(254, 245, 231, 0.4);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    /* background-color: rgb(201, 46, 46); */
  }

.row-2 .card .image-container {
    width: 100%;
    height: 60%;
    max-width: 100%;
}


.row-2 .card .image-container img {
    width: 100%;
    height: 60%;
    max-width: 100%;
}

.image-container p:nth-child(2) {
    text-align: center;
    padding:10px;
    letter-spacing: 5px;
}

.image-container p:nth-child(4) {
    text-align: center;
    padding: 0 15px 0 15px;
    font-size: 15px;
}


.image-container p:nth-child(5) {
    font-size: 15px;
    text-align: center;
    padding: 10px;
}


.home-main-content .content-row-3{
    width: 100%;
    height: 400px;
    position: relative;
    /* background:rgb(5, 5, 156); */
}

.home-main-content .content-row-3 .heading-container{
    position: absolute;
    top: 30%;
    left: 13%;
    width: 75%;
    margin: 0 auto;
}

.content-row-3 .heading-container p:nth-child(1){
    display: block;
    width: 45%;
    margin: 0 auto;
    font-size: 30px;
    letter-spacing: 4px;
}

.content-row-3 .heading-container p:nth-child(2){
    font-size: 16px;
    padding: 20px; 
}

.content-row-3 .heading-container div {
    position: relative;
    display: block;
    margin: 0px auto;
    padding: 13px;
    font-weight: 300;
    font-size: 14px;
    font-family: Verdana;
    color: #fff;
    width: 120px;
    height: auto;
    text-align: center;
    background-color: rgb(160, 13, 13);
  }

  .footer {
      width: 100%;
      height: auto;
      padding :30px 0 0 0;
      background-color: rgba(53, 53, 51, 1.9);
  }

  .footer .content .row-1 {
      display: flex;
      flex-direction: row;
      width: 1200px;
      color: rgb(158, 158, 156);
      margin:0 auto;
      height: auto;
  }

  .footer .footer-rule {
    width:1px; 
    height:180px; 
    margin:30px auto;
    background-color:#9E9E9C;
  }

  .footer .content .row-1 .col-1 {
      
    width: 30%;
    height: auto;
    margin: 0 20px;
    padding: 10px;  
    /* background-color: #fff; */
  }

  .footer .content .row-1 .col-2 {
      
    width: 30%;
    height: auto;
    margin: 0 20px;
    padding: 10px;
    /* background-color: #fff; */
  }

  .footer .content .row-1 .col-3 {
      
    width: 50%;
    height: auto;
    margin: 0 20px;  
    padding: 10px;
    /* background-color: #fff; */
  }

  .footer .content .row-1 .col-1 p:nth-child(1){
    letter-spacing: 4px;
    font-size: 20px;
  }

  .footer .content .row-1 .col-1 p:nth-child(2){
    font-size: 13px;
  }

  .footer .content .row-1 .col-2 p:nth-child(1){
    letter-spacing: 4px;
    font-size: 20px;
  }

  .footer .content .row-1 .col-3 p:nth-child(1){
    letter-spacing: 4px;
    font-size: 20px;
  }

  .footer .content .row-1 .col-2 p:nth-child(2){
    font-size: 13px;
  }
  .footer .content .row-1 .col-2 p:nth-child(3){
    font-size: 13px;
    display: block;
    width: 100%;
  }

  .footer .content .row-1 .col-2 p:nth-child(4){
    font-size: 13px;
  }

  .footer .content .footer-field .text {
    width: 100%;
    height: 35px;
    padding: 0 10px;
    font-size: 14px;
    background-color: rgba(53, 53, 51, 1.9);
    margin: 0 0px;
    border: 1px solid #9E9E9C;
    box-sizing: border-box;
  }

  .footer .content .footer-field .submit {

    width: 100%;
    height: 35px;
    color:rgba(53, 53, 51, 0.9);
    background: rgb(158, 158, 156);
    margin: 15px 0;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .footer-field .submit #subscribe{
    font-size: 17px;
    letter-spacing: 0;
    text-align: center;
  }

  .footer .content .social-container {
    width: 100%;
    height:auto;
  }

  .footer .content .social-container .social-list {
    width: 145px;
    margin: 0;
    padding: 0;
  }

  .footer .content .row-2 {
    background-color: rgba(44, 44, 44, 1.9);
    width:100%;
    color: rgb(179, 179, 175);
  }
  .footer .content .row-2 .col-1{
    width: 100%;
    position: relative;
    left: 20%;
  }

  /* ----------------------- Responsive css ---------------- */

  @media screen and (max-width: 900px){

    .mySlides img {
      width:100%;
      height:60vh;
      transition: opacity 300ms ease-in-out;
    }

    .heading-container {
      position: absolute;
      top: 20%;
      left: 2%;
      width: 90%;
      margin: 0 auto;
    }

    .content-row-3  {
      
      margin: 0;
      padding: 0;
      width: 50px;
      height: 20px;
      /* background-color:#dde; */
 
    }

    .content-row-3 #below-heading {
      position: absolute;
      top: 20%;
      left: 0;
      width: 90%;
      margin: 0 auto;
      /* background-color: aqua; */
    }

    .content-row-3 .heading-container p:nth-child(2){
      font-size: 16px;
      padding: 0 auto; 
  }

    .content-row-3 .heading-container  p:nth-child(1){
      font-size: 21px;
      width: 100%;
      display: block;
      margin: 0 auto;
    }

    .content-row-3 .heading-container  p:nth-child(2){
      display: block;
      width: 90%;
      font-size: 17px;
    }

    .heading-container p:nth-child(1) {
      font-size: 35px;
      width: 90%;
      display: block;
      margin: 0 auto;
    }

    .heading-container p:nth-child(2) {
      display: block;
      width: 90%;
      font-size: 17px;
      /* background-color: #9E9E9C; */
    }

    .heading-container div {
      margin: 0px auto;
      padding: 10px;
      font-size: 14px;
      width: 120px;

    }
    .home-main-content .content-row-1 .row-1{
          flex-direction: column;
          align-items: center;
          justify-content: center;
    }

    .home-main-content .content-row-1 .row-1 .col-1{
          width: 100%;
          height: auto;
          margin: 0 30px;
    }

    .home-main-content .content-row-1 .row-1 .col-1{
          margin: 20px auto;
    }

    .content-rule { 
        width:50%;
        height:1px;
        background-color:rgb(189, 158, 101);
    }

    .home-main-content .content-row-1 .row-1 .col-1 p {
        display: block;
        font-size: 18px;
        text-align: center;

    }
    
    .home-main-content .content-row-1 .row-1 .col-1 .scope {
        font-weight: bold;
        line-height: 0ch;
    }
    
    .home-main-content .content-row-1 .row-1 .col-1 .heading {
        font-weight: bold; 
        font-size: 25px;
    }

    .home-main-content .content-row-2 .row-1 p:nth-child(1){
    
        width: 100%;
        padding: 30px 0px 0px 0px;
        font-size: 25px;
    }
  
    .home-main-content .content-row-2 .row-1 p:nth-child(2){
      width: 100%;
      font-size: 18px;
      padding: 30px 0px 0px 0px;
    
    }

    .home-main-content .content-row-2 .row-2  {
      width: 90%;
      flex-direction: column;
  }

    .home-main-content .content-row-2 .row-2 .card  {
      width: 100%;
      height: auto;
    }


  .footer {
      width: 100%;
      height: auto;
      background-color: rgba(53, 53, 51, 1.9);
  }

  .footer .content .row-1 {
      flex-direction: column;
